import { PATCH, POST } from "../UrlConfig/MethodType";

export const staffOneInstituteListFunction = (builder) => {
  return builder.query({
    query: (data) =>
      `/ins/${data.id}/approve-staff/list?page=${data.page}&limit=${
        data.limit
      }&date=${data.date ? data.date : ""}`,
    serializeQueryArgs: ({ endpointName }) => {
      return endpointName;
    },
    transformResponse: (response) => response?.staffIns,
    forceRefetch({ currentArg, previousArg }) {
      return currentArg !== previousArg;
    },
  });
};

