import {
  useStaffOneInstituteListQuery,
  useStaffGetAttendanceQuery,
  useStaffMarkAttendanceMutation,
  useStaffUpdateAttendanceMutation,
  useStaffOneAttendanceCalendarQuery,
  useStaffTodayAttendaceStatsQuery,
  useStaffTodayAttendaceStaffListStatsQuery,
  useStaffTimetableStatsQuery,
  useStaffTimetableSubjectListStatsQuery,
  useStaffTeachingPlanStatsQuery,
  useStaffTeachingPlanSubjectListStatsQuery,
  useStaffAttendaceProfileStatsQuery,
} from "../../../Services/StaffManager/staffApiDynamic";

export const useStaffOneInstituteList = (args) => {
  const { data, refetch, isFetching } = useStaffOneInstituteListQuery(
    args.data,
    {
      skip: args.skip,
    }
  );
  return {
    staffOneInstituteList: data,
    staffOneInstituteListRefetch: refetch,
    staffOneInstituteListLoading: isFetching,
  };
};

export const useStaffGetAttendance = (args) => {
  const { data, refetch, isFetching } = useStaffGetAttendanceQuery(args.data, {
    skip: args.skip,
  });
  return {
    staffGetAttendance: data,
    staffGetAttendanceRefetch: refetch,
    staffGetAttendanceLoading: isFetching,
  };
};

export const useStaffMarkAttendance = () => {
  const [staffMarkAttendance] = useStaffMarkAttendanceMutation();
  return [staffMarkAttendance];
};
export const useStaffUpdateAttendance = () => {
  const [staffUpdateAttendance] = useStaffUpdateAttendanceMutation();
  return [staffUpdateAttendance];
};

