import style from "./StaffCalender.module.css";
import {
  dayName,
  getCompareToday,
  useCalendar,
} from "../../Hooks/CustomHooks/use-calendar";
import { assestsUtilityUrl } from "../../Services/UrlConfig/AssestsBaseUrl";
import dayjs from "dayjs";
import { useEffect } from "react";
const StaffCalender = ({ list, isMonth, onChange }) => {
  const { selectedDate, getPreviousMonth, getNextMonth, generateCalendar } =
    useCalendar();
  const onDateChoose = () => {};

  const onPrev = () => {
    getPreviousMonth();
  };
  const onNext = () => {
    getNextMonth();
  };
  useEffect(() => {
    if (isMonth) {
      onChange(selectedDate);
    }
  }, [isMonth, selectedDate]);
  // console.info("selectedDate", selectedDate);
  return (
    <div className={style.calendar}>
      <div className={style.calendar_top}>
        <img
          src={`${assestsUtilityUrl}/next.svg`}
          alt="previous icon"
          onClick={onPrev}
          style={{
            rotate: "180deg",
          }}
        />
        <h6>
          <span>{dayjs(selectedDate).format("MMMM")}</span>{" "}
          <span>{dayjs(selectedDate).format("YYYY")}</span>
        </h6>
        <img
          src={`${assestsUtilityUrl}/next.svg`}
          alt="next icon"
          onClick={onNext}
        />
      </div>

      <div>
        <div className={style.calendar_days}>
          {dayName.map((name) => (
            <p className={style.calendar_days_label} key={name}>
              {name}
            </p>
          ))}
        </div>
        {Object.values(generateCalendar)?.map((cols, index) => (
          <div className={style.calendar_days} key={index}>
            {cols.map((cl) => (
              <p
                className={style.calendar_days_value}
                key={cl?.date}
                style={{
                  "--calendar-border": getCompareToday(cl?.date)
                    ? "#420789"
                    : "",
                }}
                onClick={() => onDateChoose(cl?.date)}
              >
                {cl?.value}
                <div
                  className={style.calendar_dot}
                  style={{
                    "--dot-color": list
                      ? date_color[list[getDateModify(cl?.date)]]
                      : "",
                    // for leave
                    // "--dot-color": "#00308F",
                    // for holiday
                    // "--dot-color": "#cacaca",
                    // for absent
                    // "--dot-color": "#ee3833",
                    //   for present
                    // "--dot-color": "rgba(37, 172, 74, 0.8)",
                  }}
                ></div>
              </p>
            ))}
          </div>
        ))}
      </div>
    </div>
  );
};

export default StaffCalender;

const date_color = {
  P: "rgba(37, 172, 74, 0.8)",
  L: "#00308F",
  A: "#ee3833",
  H: "#cacaca",
};

const getDateModify = (dt) => {
  let sp = dt?.split("-");
  let d = sp?.[0] > 9 ? sp?.[0] : `0${sp?.[0]}`;
  let m = sp?.[1] > 9 ? sp?.[1] : `0${sp?.[1]}`;
  return `${d}/${m}/${sp?.[2]}`;
};
