import { useTranslation } from "react-i18next";
import style from "../StaffProfile.module.css";
import { assestsUtilityUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import StaffCalender from "../../StaffCalender/StaffCalender";
import { useState } from "react";
import AttendanceSelectDateRange from "./AttendanceSelectDateRange";
import { useStaffAttendaceProfileStats } from "../../../Hooks/ApiHooks/StaffManager/staff-manager-api";
import QvipleLoader from "../../../Loader/QvipleLoader";

const StaffProfileAttendanceRangeWise = ({ step, setStep, sid }) => {
  const { t } = useTranslation();
  const [selectRange, setSelectRange] = useState(true);
  const [state, setState] = useState({
    from: "",
    to: "",
  });

  const { staffAttendaceProfileStats, staffAttendaceProfileStatsLoading } =
    useStaffAttendaceProfileStats({
      data: {
        sid: sid,
        from: state.from,
        to: state.to,
      },
      skip: sid ? !(state.from && state.to) : !sid,
    });

  const onClose = () => {
    setSelectRange(() => false);
  };
  //   console.info("state", staffAttendaceProfileStats);
  return (
    <>
      <div className={style.spa_card}>
        <div className={style.spa_calendar}>
          <StaffCalender list={staffAttendaceProfileStats?.dates} />

          <div className={style.spac_inner}>
            <div
              className={style.shades}
              style={{
                "--text-color": "#ffffff",
                "--bg-color": "rgba(37, 172, 74, 0.8)",
              }}
            >
              {t("present")}
            </div>
            <div
              className={style.shades}
              style={{
                "--text-color": "#ffffff",
                "--bg-color": "#ee3833",
              }}
            >
              {t("absent")}
            </div>
            <div
              className={style.shades}
              style={{
                "--text-color": "#121212",
                "--bg-color": "#cacaca",
              }}
            >
              {t("holiday")}
            </div>
            <div
              className={style.shades}
              style={{
                "--text-color": "#ffffff",
                "--bg-color": "#00308F",
              }}
            >
              {t("leave")}
            </div>
          </div>
        </div>
        <div className={style.spac_other}>
          <div className={style.spac_container}>
            <div
              className={style.spac_container_inner}
              onClick={() => setStep(1)}
            >
              <p>{t("monthly")}</p>
              <img
                src={
                  step === 1
                    ? `${assestsUtilityUrl}/radio-blue.svg`
                    : `${assestsUtilityUrl}/radio-grey.svg`
                }
                alt="radio icon of attendance"
              />
            </div>
            <div
              className={style.spac_container_inner}
              onClick={() => setStep(2)}
            >
              <p>{t("range")}</p>
              <img
                src={
                  step === 2
                    ? `${assestsUtilityUrl}/radio-blue.svg`
                    : `${assestsUtilityUrl}/radio-grey.svg`
                }
                alt="radio icon of attendance"
              />
            </div>
            <div
              className={style.spac_container_inner}
              onClick={() => setStep(3)}
            >
              <p>{t("yearly")}</p>
              <img
                src={
                  step === 3
                    ? `${assestsUtilityUrl}/radio-blue.svg`
                    : `${assestsUtilityUrl}/radio-grey.svg`
                }
                alt="radio icon of attendance"
              />
            </div>
          </div>
          <div
            className={style.spac_count}
            style={{
              "--bg-sa-color": "rgba(37, 172, 74, 0.12)",
            }}
          >
            <p>{t("present_count")}</p>
            <p>
              {staffAttendaceProfileStats?.present ?? 0} {` `} {t("days")}
            </p>
            <p>{staffAttendaceProfileStats?.present_percentage ?? 0}%</p>
          </div>
          <div
            className={style.spac_count}
            style={{
              "--bg-sa-color": "rgba(238, 56, 51, 0.12)",
            }}
          >
            <p>{t("absent_count")}</p>
            <p>
              {staffAttendaceProfileStats?.absent ?? 0}
              {` `} {t("days")}
            </p>
            <p>{staffAttendaceProfileStats?.absent_percentage ?? 0}%</p>
          </div>
          <div
            className={style.spac_count}
            style={{
              "--bg-sa-color": "rgba(37, 172, 74, 0.12)",
            }}
          >
            <p>{t("leave_with_pay")}</p>
            <p>
              {staffAttendaceProfileStats?.leave_with_pay ?? 0}
              {` `} {t("days")}
            </p>
            <p>{staffAttendaceProfileStats?.leave_with_pay_percentage ?? 0}%</p>
          </div>
          <div
            className={style.spac_count}
            style={{
              "--bg-sa-color": "rgba(37, 172, 74, 0.12)",
            }}
          >
            <p>{t("on_duty_leave")}</p>
            <p>
              {staffAttendaceProfileStats?.on_duty_leave ?? 0}
              {` `} {t("days")}
            </p>
            <p>{staffAttendaceProfileStats?.on_duty_leave_percentage ?? 0}%</p>
          </div>
        </div>
      </div>
      {staffAttendaceProfileStatsLoading && <QvipleLoader />}
      {selectRange && (
        <AttendanceSelectDateRange onClose={onClose} onSelect={setState} />
      )}
    </>
  );
};

export default StaffProfileAttendanceRangeWise;
