import { useState } from "react";
import StaffProfileAttendanceMonthly from "./StaffProfileAttendanceMonthly";
import StaffProfileAttendanceYearly from "./StaffProfileAttendanceYearly";
import StaffProfileAttendanceRangeWise from "./StaffProfileAttendanceRangeWise";
const StaffProfileAttendance = ({ sid }) => {
  const [step, setStep] = useState(1);

  // dayjs().format("DD/MM/YYYY")
  return (
    <>
      {step === 1 ? (
        <StaffProfileAttendanceMonthly
          step={step}
          setStep={setStep}
          sid={sid}
        />
      ) : step === 2 ? (
        <StaffProfileAttendanceRangeWise
          step={step}
          setStep={setStep}
          sid={sid}
        />
      ) : step === 3 ? (
        <StaffProfileAttendanceYearly step={step} setStep={setStep} sid={sid} />
      ) : null}
    </>
  );
};

export default StaffProfileAttendance;
